import React, { Component } from "react";
import Video from "../Components/Video/Video";
import RemoteVideo from "../Components/RemoteVideo/RemoteVideo";
import { connect } from "react-redux";
import LocalVideo from "../Components/LocalVideo/LocalVideo";

class SliderLeft extends Component {
  render() {
    return (
      <div style={{ width: "100%", height: "100%" }}>
        <LocalVideo />
      </div>
    );
  }
}

function mapStateToProps(state) {
  return {
    streamState: state.streamState,
  };
}

function mapDispatchToProps(dispatch) {
  return {
    // setModal: (data) => dispatch(setModal(data)),
    // setpdfViewer: (data) => dispatch(setpdfViewer(data)),
  };
}

export default connect(mapStateToProps, null)(SliderLeft);
