export const ACTION_TYPES = {
  SET_STREAM: "SET_STREAM",
};
const initializeStream = { local: [], remote: [] };

const streamReducer = (state = initializeStream, action) => {
  switch (action.type) {
    case ACTION_TYPES.SET_STREAM:
      state = action.payload;
      return state;

    default:
      return state;
  }
};

const setStream = (stream) => {
  return {
    type: ACTION_TYPES.SET_STREAM,
    payload: stream,
  };
};

export { streamReducer, setStream };
