import React, { Component, PureComponent } from "react";

class Video extends PureComponent {
  componentDidMount = () => {
    this.videoRef.srcObject = this.props?.srcObject;
  };

  componentDidUpdate = (prevProps) => {
    if (prevProps !== this.props) {
      this.videoRef.srcObject = this.props?.srcObject;
    }
  };

  onClick=()=>{
      this.props.onClick(this.videoRef)
  }

  render() {
    console.log(this.props)

    return (
      <video
        id="video"
        autoPlay={true}
        ref={(ref) => {
          this.videoRef = ref;
        }}
        width={"100%"}
        height={100}
        style={{ background: "black", paddingTop: 5 }}
        onClick={this.onClick}
      ></video>
    );
  }
}

export default Video;
