import React, { Component } from 'react'
import PropTypes from 'prop-types'
import './style.scss'
import { RoomClient } from '../../module/RoomClient';

export default class CameraOption extends Component {
          // static propTypes = {
          //           prop: PropTypes
          // }

          constructor(props) {
                    super(props);
                    this.state = { video: [] };
          }


          componentDidMount = async () => {
                    const devices = await window.navigator.mediaDevices.enumerateDevices();
                    const video = [];
                    for (let index = 0; index < devices.length; index++) {
                              const deviceInfo = devices[index];
                              if (deviceInfo.kind === 'videoinput') {
                                        video.push({ deviceInfo, active: false })
                              }


                    }
                    this.setState({ video })
          }

          onActiveOptionCamera = async (item, index) => {
                    const currentVideo = this.state.video;
                    for (let i = 0; i < currentVideo.length; i++) {
                              const element = currentVideo[i];
                              if (i === index) {
                                        element.active = true;
                                        await window.roomClient?.closeProducer(RoomClient.mediaType.video)
                                        window.roomClient?.produce(RoomClient.mediaType.video, element.deviceInfo.deviceId)
                              } else {
                                        element.active = false;
                              }
                    }
                    this.setState({ video: currentVideo })
          }

          render() {
                    return (
                              <div className="micOption" key="camera">
                                        <div className="row"><i className="material-icons"> videocam</i><h2 >Videos</h2></div>
                                        {this.state.video.map((item, index) => {
                                                  return <div onClick={() => this.onActiveOptionCamera(item, index)} key={"camera" + index}> <div className="row padding">{item.active ? <i className="material-icons"> check</i> : <i className="material-icons"> videocam</i>}<span >{item.deviceInfo.label}</span></div></div>
                                        })}
                              </div>
                    )
          }
}