import { Button, Col, Input, Row, Form } from "antd";
import React, { Component } from "react";
import { connect } from "react-redux";
import { Layout } from "antd";
import Avatar from "antd/lib/avatar/avatar";

const { Footer, Content } = Layout;
class Chat extends Component {
  sendMessage = (values) => {
    window.roomClient.sendMessageChat(values.message);
    this.formRef?.resetFields();
  };
  render() {
    return (
      <Layout style={{ width: 350, height: "100%" }}>
        <Content style={{ width: "100%" }}>
          {this.props.messageState.map((item, index) => {
            return (
              <Row key={index}>
                <Col span={3} style={{ alignItems: "flex-end", display: "flex", padding: 5 }}>
                  {item.id === window.roomClient.socket.id ? null : <Avatar src="./icons/avatar.png" />}
                </Col>
                <Col span={21}>
                  {item.id === window.roomClient.socket.id ? null : (
                    <Row style={{ width: "100%" }}>
                      <div style={{ fontSize: 11, color: "#90949c" }}>{item.name}</div>
                    </Row>
                  )}

                  <Row style={{ width: "100%" }}>
                    <div
                      style={
                        item.id === window.roomClient.socket.id
                          ? {
                              marginTop: 2,
                              marginBottom: 2,
                              marginRight: 5,
                              background: "#42a5f5",
                              color: "#fff",
                              borderRadius: "20px 20px 3px 20px",
                              display: "block",
                              maxWidth: "75%",
                              padding: "7px 13px 7px 13px",
                              marginLeft: "auto",
                              fontSize: 13,
                            }
                          : {
                              marginTop: 2,
                              marginBottom: 2,
                              background: "#ddd",
                              borderRadius: "20px 20px 20px 3px",
                              display: "block",
                              maxWidth: "75%",
                              padding: "7px 13px 7px 13px",
                              float: "left",
                              fontSize: 13,
                              color: "#444950",
                            }
                      }
                    >
                      {item.message}
                    </div>
                  </Row>
                </Col>
              </Row>
            );
          })}
        </Content>
        <Footer style={{ padding: 0, margin: 10, display: "flex", justifyContent: "center", alignItems: "center" }}>
          <Form ref={(ref)=>this.formRef=ref} layout="inline" name="horizontal_login" onFinish={this.sendMessage}>
            <Form.Item name="message">
              <Input placeholder="Nhp tin nhn" />
            </Form.Item>
            <Form.Item>
              <Button
                style={{ justifyContent: "center", alignItems: "center", display: "flex" }}
                type="primary"
                htmlType="submit"
                icon={<i className="material-icons">send</i>}
              ></Button>
            </Form.Item>
          </Form>
        </Footer>
      </Layout>
    );
  }
}
function mapStateToProps(state) {
  return {
    messageState: state.messageState,
  };
}

export default connect(mapStateToProps, null)(Chat);
