export const ACTION_TYPES = {
  SET_LARGE_VIDEO: "SET_LARGE_VIDEO",
};
const initializeStream = null;

const largeVideoReducer = (state = initializeStream, action) => {
  switch (action.type) {
    case ACTION_TYPES.SET_LARGE_VIDEO:
      state = action.payload;
      return state;

    default:
      return state;
  }
};

const setLargeVideo = (stream) => {
  return {
    type: ACTION_TYPES.SET_LARGE_VIDEO,
    payload: stream,
  };
};

export { largeVideoReducer, setLargeVideo };
