export const ACTION_TYPES = {
    SET_LOCAL_ELEMENT: "SET_LOCAL_ELEMENT",
};
const initializeStream = { videos: [], audio: [], screenCast: [] };

const localElementReducer = (state = initializeStream, action) => {
    switch (action.type) {
        case ACTION_TYPES.SET_LOCAL_ELEMENT:
            state = action.payload;
            return state;

        default:
            return state;
    }
};

const setLocalElementReducer = (element) => {
    return {
        type: ACTION_TYPES.SET_LOCAL_ELEMENT,
        payload: element,
    };
};

export { localElementReducer, setLocalElementReducer };
