import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'
import Clock from '../Clock/Clock';

class LargeVideo extends Component {

          // static propTypes = {
          //           prop: PropTypes
          // }

          componentDidMount = () => {
                    this.videoRef.srcObject = this.props.largeVideoState?.stream;
          }

          componentDidUpdate = (prevProps) => {
                    console.log(this.props.largeVideoState?.stream)

                    if (prevProps.largeVideoState !== this.props.largeVideoState) {
                              this.videoRef.srcObject = this.props.largeVideoState?.stream;
                    }
          }

          render() {
                    return (
                              <div style={{ position: "relative", width: "100%", height: "100%", background: "black" }}>
                                        <Clock />
                                        <video autoPlay ref={(el) => { this.videoRef = el }} style={{ position: "relative", width: "100%", height: "100%", background: "black" }} >
                                        </video>
                              </div>

                    )
          }
}
function mapStateToProps(state) {
          return {
                    largeVideoState: state.largeVideoState,
          };
}

function mapDispatchToProps(dispatch) {
          return {
                    // setModal: (data) => dispatch(setModal(data)),
                    // setpdfViewer: (data) => dispatch(setpdfViewer(data)),
          };
}

export default connect(mapStateToProps, null)(LargeVideo);
