import React, { Component } from "react";
import { connect } from "react-redux";
import { userRoom } from "../../global/UserRoom";
import { setStream } from "../../reducers/streamReducer";
import { setViewOneCamera } from "../../reducers/uiReducer";
import { setUserOnRoom } from "../../reducers/userOnRoomReducer";
import MediaElement from "./MediaElement";
import "./style.scss";
class MultipleMediaContainer extends Component {
  constructor(props) {
    super(props);
    this.state = {
      widthMediaElement: 0,
    };
  }
  componentDidMount = () => {
    const { userOnRoomState } = this.props;
    if (userOnRoomState.length < 5) {
      this.setState({
        widthMediaElement: 100 / userOnRoomState.length,
      });
    } else {
      this.setState({
        widthMediaElement: 100 / 4,
      });
    }

  };
  componentDidUpdate = (prevProps) => {
    const { userOnRoomState } = this.props;
    if (prevProps.userOnRoomState !== userOnRoomState) {
      if (userOnRoomState.length < 5) {
        this.setState({
          widthMediaElement: 100 / userOnRoomState.length,
        });
      } else {
        this.setState({
          widthMediaElement: 100 / 4,
        });
      }
      if (userOnRoomState.length < 2) {
        this.props.setViewOneCamera(true)
      }
    }
  };


  onHandleSetLargeVideo = (item, index) => {
    this.setActive(index)
  }


  setActive = (index) => {
    const currentStream = { ...this.props.streamState };
    const local = currentStream.local;
    for (let j = 0; j < local.length; j++) {
      const element = local[j];
      element.active = false;
    }
    const clone=Array.from(this.props.userOnRoomState);
    for (let i = 0; i <clone.length; i++) {
      const element = clone[i];
      if (i === index) {
        element.active = true;
      } else {
        element.active = false;
      }
    }
    this.props.setUserOnRoom(clone)
    this.props.setStream({ ...currentStream })
  }


  render() {
    const { userOnRoomState } = this.props;
    const { widthMediaElement } = this.state;
    return (
      <div className="multipleMedia__container">
        {userOnRoomState.map((item, index) => {
          return <MediaElement item={item} key={index} elementWidth={widthMediaElement} onHandleSetLargeVideos={this.onHandleSetLargeVideo} />;
        })}
      </div>
    );
  }
}
function mapStateToProps(state) {
  return {
    viewOneCamera: state.ui.viewOneCamera,
    userOnRoomState: state.userOnRoomState,
    streamState: state.streamState
  };
}

function mapDispatchToProps(dispatch) {
  return {
    setViewOneCamera: (data) => dispatch(setViewOneCamera(data)),
    setStream: (data) => dispatch(setStream(data)),
    setUserOnRoom: (data) => dispatch(setUserOnRoom(data))
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(MultipleMediaContainer);
