import { message, notification } from "antd";
import { EVENTS, RTC_EVENTS } from "../events/EVENTS";
import { Role } from "../global/Role";
import { setUserRoom, userRoom } from "../global/UserRoom";
import { setLargeVideo } from "../reducers/largeVideoReducer";
import { setMessage } from "../reducers/messageReducer";
import { setRemoteStream } from "../reducers/remoteReducer";
import { setStream } from "../reducers/streamReducer";
import { setUserOnRoom } from "../reducers/userOnRoomReducer";
import { store } from "../redux/store";

const mediaType = {
  audio: "audioType",
  video: "videoType",
  screen: "screenType",
};
const _EVENTS = {
  exitRoom: "exitRoom",
  openRoom: "openRoom",
  startVideo: "startVideo",
  stopVideo: "stopVideo",
  startAudio: "startAudio",
  stopAudio: "stopAudio",
  startScreen: "startScreen",
  stopScreen: "stopScreen",
};

class RoomClient {
  constructor(mediasoupClient, socket, room_id, name, password, successCallback) {
    this.name = name;
    this.mediasoupClient = mediasoupClient;
    this.socket = socket;
    this.producerTransport = null;
    this.consumerTransport = null;
    this.device = null;
    this.room_id = room_id;
    this.consumers = new Map();
    this.producers = new Map();
    this.producerLabel = new Map();
    this._isOpen = false;
    this.eventListeners = new Map();
    Object.keys(_EVENTS).forEach((evt) => {
      this.eventListeners.set(evt, []);
    });
    this.password = password;
    this.createRoom(room_id, password).then(async () => {
      await this.join(name, room_id, password);
      console.log("join")
      await this.initSockets();
      this._isOpen = true;
      console.log("successCallback")
      successCallback();
    });
  }

  createRoom = async (roomName, password) => {
    const reponse = await this.socket.request(EVENTS.SOCKET_CREATE_ROOM, { roomName, password });
    console.log(reponse)
  };

  join = async (name, room_id, password) => {
    await this.socket.request(EVENTS.SOCKET_USER_JOIN_ROOM, { username: name, roomName: room_id, password });
    const data = await this.socket.request(RTC_EVENTS.GET_ROUTER_CAPABILITIES);
    console.log(data)
    let device = await this.loadDevice(data);
    this.device = device;
    await this.initTransports(device);
    // await this.produce(mediaType.video, null)
    const myRoom = await this.socket.request(EVENTS.SOCKET_GET_ROOM_INFO);
    const newMap = new Map()
    console.log("myRoom", myRoom)

    for (let index = 0; index < myRoom.length; index++) {
      if (myRoom[index].socketId !== this.socket.id) {
        console.log("vao day")
        const currentMap = store.getState().streamRemoteState.get(myRoom[index].socketId)
        newMap.set(myRoom[index].socketId, { active: false, audio: currentMap?.audio, video: currentMap?.video })
      }
    }
    store.dispatch(setRemoteStream(newMap))
    store.dispatch(setUserOnRoom(myRoom.filter(value => value.id !== this.socket.id)))
    await this.socket.emit(RTC_EVENTS.GET_PRODUCER);
  };

  loadDevice = async (routerRtpCapabilities) => {
    let device;
    try {
      device = new this.mediasoupClient.Device();
    } catch (error) {
      if (error.name === "UnsupportedError") {
        console.error("browser not supported");
      }
      console.error(error);
    }
    await device.load({
      routerRtpCapabilities,
    });
    return device;
  };

  initTransports = async (device) => {
    // init producerTransport
    {
      const data = await this.socket.request(RTC_EVENTS.CREATE_TRANSPORT, {
        forceTcp: false,
        rtpCapabilities: device.rtpCapabilities,
      });

      if (data.error) {
        console.error(data.error);
        return;
      }
      this.producerTransport = device.createSendTransport(data);

      this.producerTransport.on("connect", async ({ dtlsParameters }, callback, errback) => {
        console.log(dtlsParameters)
        try {
          await this.socket.request(RTC_EVENTS.CONNECT_TRANSPORT, { dtlsParameters, transport_id: data.id });
          callback();
        } catch (error) {
          errback(error);
        }
      });

      this.producerTransport.on("produce", async ({ kind, rtpParameters }, callback, errback) => {
        try {
          const { producer_id, producer_name } = await this.socket.request(RTC_EVENTS.CREATE_PRODUCE, {
            producerTransportId: this.producerTransport.id,
            kind,
            rtpParameters,
          });
          callback({ id: producer_id, producer_name });
        } catch (err) {
          errback(err);
        }
      });

      this.producerTransport.on("connectionstatechange", (state) => {
        switch (state) {
          case "connecting":
            break;
          case "connected":
            //localVideo.srcObject = stream
            break;
          case "failed":
            this.producerTransport.close();
            break;
          default:
            break;
        }
      });
    }

    // init consumerTransport
    {
      const data = await this.socket.request(RTC_EVENTS.CREATE_TRANSPORT, { forceTcp: false });
      if (data.error) {
        console.error(data.error);
        return;
      }
      // only one needed
      this.consumerTransport = device.createRecvTransport(data);
      this.consumerTransport.on("connect", async ({ dtlsParameters }, callback, errback) => {
        try {
          await this.socket.request(RTC_EVENTS.CONNECT_TRANSPORT, { transport_id: this.consumerTransport.id, dtlsParameters });
          callback();
        } catch (error) {
          errback(errback);
        }
      });

      this.consumerTransport.on("connectionstatechange", async (state) => {
        switch (state) {
          case "connecting":
            break;

          case "connected":
            //remoteVideo.srcObject = await stream;
            //await socket.request('resume');
            break;

          case "failed":
            this.consumerTransport.close();
            break;

          default:
            break;
        }
      });
    }
  };

  initSockets = () => {
    this.socket.on("incomingMessage", (data) => {
      console.log(data)
      store.dispatch(setMessage(data));
    })

    this.socket.on("userJoinRoom", async ({ name, id }) => {
      notification["info"]({
        message: 'Thng bo',
        description: name + "  tham gia vo cuc hp",
        placement: "bottomRight"
      });
      const myRoom = await this.socket.request("getMyRoomInfo");
      const newMap = new Map()
      for (let index = 0; index < myRoom.length; index++) {
        if (myRoom[index].id !== this.socket.id) {
          const currentMap = store.getState().streamRemoteState.get(myRoom[index].id)
          newMap.set(myRoom[index].id, { active: false, audio: currentMap?.audio, video: currentMap?.video })
        }
      }
      store.dispatch(setUserOnRoom(myRoom.filter(value => value.id !== this.socket.id)))
      store.dispatch(setRemoteStream(newMap))

    });
    this.socket.on("userLeftRoom", async ({ name, id }) => {
      notification["warning"]({
        message: 'Thng bo',
        description: name + "  ri khi cuc hp",
        placement: "bottomRight"
      });
      const myRoom = await this.socket.request("getMyRoomInfo");
      const newMap = new Map()
      for (let index = 0; index < myRoom.length; index++) {
        if (myRoom[index].id !== this.socket.id) {
          const currentMap = store.getState().streamRemoteState.get(myRoom[index].id)
          newMap.set(myRoom[index].id, { active: false, audio: currentMap?.audio, video: currentMap?.video })
        }
      }
      store.dispatch(setUserOnRoom(myRoom.filter(value => value.id !== this.socket.id)))
      store.dispatch(setRemoteStream(newMap))

    });
    this.socket.on("consumerClosed", ({ consumer_id }) => {
      console.log("closing consumer:", consumer_id);
      this.removeConsumer(consumer_id);
    });
    this.socket.on(RTC_EVENTS.NEW_PRODUCER, async (data) => {
      console.log(data)
      for (let index = 0; index < data.length; index++) {
        const { producer_id, peerName, peerId } = data[index];
        await this.consume(producer_id, peerName, peerId);
      }
    });

    this.socket.on("disconnect", () => {
      this.exit(false);
    });
  };

  produce = async (type, deviceId = null) => {
    let mediaConstraints = {};
    let audio = false;
    let screen = false;
    switch (type) {
      case mediaType.audio:
        mediaConstraints = {
          audio: {
            deviceId: deviceId,
          },
          video: false,
        };
        audio = true;
        break;
      case mediaType.video:
        mediaConstraints = {
          audio: false,
          video: {
            width: {
              min: 640,
              ideal: 1920,
            },
            height: {
              min: 400,
              ideal: 1080,
            },
            deviceId: deviceId,
            /*aspectRatio: {
                                        ideal: 1.7777777778
                                    }*/
          },
        };
        break;
      case mediaType.screen:
        mediaConstraints = false;
        screen = true;
        break;
      default:
        return;
        break;
    }
    if (!this.device.canProduce("video") && !audio) {
      console.error("cannot produce video");
      return;
    }
    if (this.producerLabel.has(type)) {
      console.log("producer already exists for this type " + type);
      return;
    }
    let stream;
    try {
      stream = screen ? await navigator.mediaDevices.getDisplayMedia() : await navigator.mediaDevices.getUserMedia(mediaConstraints);
      const track = audio ? stream.getAudioTracks()[0] : stream.getVideoTracks()[0];
      const params = {
        track,
      };
      if (!audio && !screen) {
        params.encodings = [
          { rid: 'r0', maxBitrate: 100000, scalabilityMode: 'S1T3' },
          { rid: 'r1', maxBitrate: 300000, scalabilityMode: 'S1T3' },
          { rid: 'r2', maxBitrate: 600000, scalabilityMode: 'S1T3' },
          { rid: 'r3', maxBitrate: 900000, scalabilityMode: 'S1T3' }
        ]
        params.codecOptions = {
          videoGoogleStartBitrate: 1000,
        };
      }
      this.producer = await this.producerTransport.produce(params);
      this.producers.set(this.producer.id, this.producer);

      let elem;
      if (!audio) {
        const streamState = { ...store.getState().streamState };
        const local = streamState.local;
        const remote = streamState.remote;
        local.push({
          stream,
          id: this.producer.id,
          name: this.name,
          hasVideo: true
        });
        store.dispatch(setStream({ local, remote }));
        store.dispatch(setLargeVideo({ stream }));
      }

      this.producer.on("trackended", () => {
        console.log("producer trackend")
        this.closeProducer(type);
      });

      this.producer.on("transportclose", () => {
        console.log("producer transport close");
        this.producers.delete(this.producer.id);
      });

      this.producer.on("close", () => {
        console.log("closing producer");
        this.producers.delete(this.producer.id);
      });

      this.producerLabel.set(type, this.producer.id);

      switch (type) {
        case mediaType.audio:
          this.event(_EVENTS.startAudio);
          break;
        case mediaType.video:
          this.event(_EVENTS.startVideo);
          break;
        case mediaType.screen:
          this.event(_EVENTS.startScreen);
          break;
        default:
          return;
          break;
      }
    } catch (err) {
      console.log(err);
    }
  };

  consume = async (producer_id, peerName, peerId) => {
    //let info = await roomInfo()
    let userOnRoom = this.cloneMap(store.getState().streamRemoteState)
    this.getConsumeStream(producer_id).then(({ consumer, stream, kind }) => {
      this.consumers.set(consumer.id, consumer);
      let currentUser = userOnRoom.get(peerId);

      if (!currentUser) {
        return;
      }
      if (kind === "video") {
        currentUser.video = { type: "video", stream, id: consumer.id }
      } else {
        currentUser.audio = { type: "audio", stream, id: consumer.id }
      }
      console.log("123", stream)

      store.dispatch(setRemoteStream(userOnRoom))
      consumer.on("trackended", () => {
        console.log("consumer trackend")
        this.removeConsumer(consumer.id);
      });
      consumer.on("transportclose", () => {
        console.log("consumer transportclose")
        this.removeConsumer(consumer.id);
      });
    });
  };

  getConsumeStream = async (producerId) => {
    const { rtpCapabilities } = this.device;
    const data = await this.socket.request(RTC_EVENTS.CREATE_CONSUME, {
      rtpCapabilities,
      consumerTransportId: this.consumerTransport.id, // might be
      producerId,
    });
    const { id, kind, rtpParameters } = data.params;

    const codecOptions = [
      { ssrc: 222220, scalabilityMode: 'S4T3' }
    ];
    const consumer = await this.consumerTransport.consume({
      id,
      producerId,
      kind,
      rtpParameters,
      codecOptions,
    });
    console.log(consumer.track)

    const stream = new MediaStream();
    stream.addTrack(consumer.track);
    return { consumer, stream, kind, };
  };

  closeProducer = (type) => {
    if (!this.producerLabel.has(type)) {
      console.log("there is no producer for this type " + type);
      return;
    }
    let producer_id = this.producerLabel.get(type);
    this.socket.emit("producerClosed", { producer_id, });
    this.producers.get(producer_id).close();
    this.producers.delete(producer_id);
    this.producerLabel.delete(type);

    if (type !== mediaType.audio) {
      const streamState = store.getState().streamState;
      const remote = streamState.remote.filter((value) => value.id !== producer_id);
      const local = streamState.local.filter((value) => value.id !== producer_id);
      store.dispatch(setStream({ local, remote }));
    }

    switch (type) {
      case mediaType.audio:
        this.event(_EVENTS.stopAudio);
        break;
      case mediaType.video:
        this.event(_EVENTS.stopVideo);
        break;
      case mediaType.screen:
        this.event(_EVENTS.stopScreen);
        break;
      default:
        return;
        break;
    }
  };

  pauseProducer(type) {
    if (!this.producerLabel.has(type)) {
      console.log("there is no producer for this type " + type);
      return;
    }
    let producer_id = this.producerLabel.get(type);
    this.producers.get(producer_id).pause();
  }

  resumeProducer = (type) => {
    if (!this.producerLabel.has(type)) {
      console.log("there is no producer for this type " + type);
      return;
    }
    let producer_id = this.producerLabel.get(type);
    this.producers.get(producer_id).resume();
  };

  removeConsumer = (consumer_id) => {
    let userOnRoom = this.cloneMap(store.getState().streamRemoteState);
    userOnRoom.forEach(user => {
      let { audio, video } = user;
      if (audio) {
        if (audio.id === consumer_id) {
          user.audio = null;
        }
      }

      if (video) {
        if (video.id === consumer_id) {
          user.video = null;
        }
      }

    })
    store.dispatch(setRemoteStream(userOnRoom))
    this.consumers.delete(consumer_id);
  };

  exit(offline = false) {
    let clean = () => {
      this._isOpen = false;
      this.consumerTransport.close();
      this.producerTransport.close();
      this.socket.off("disconnect");
      this.socket.off("newProducers");
      this.socket.off("consumerClosed");
      this.socket.off("userJoinRoom");
      this.socket.off("userLeftRoom");
    };

    if (!offline) {
      this.socket.request("exitRoom").then((e) => console.log(e)).catch((e) => console.warn(e)).finally(() => { clean(); });
    } else {
      clean();
    }
    store.dispatch(setStream({ local: [], remote: [] }));
    this.event(_EVENTS.exitRoom);
  }

  getAllRemoteVideo = () => {

  }

  cloneMap = (dataMap) => {
    const newMap = new Map();
    for (const [key, value] of dataMap) {
      newMap.set(key, value)
    }
    return newMap
  }

  getAllChat = async () => {
    const messages = await this.socket.request("getAllChat");
    console.log(messages)
    store.dispatch(setMessage(messages))
  }

  roomInfo = async () => {
    let info = await this.socket.request("roomInfo");
    return info;
  };

  changePassword = async () => {
    const res = await this.socket.request("changePass", Role.password);
    message.info(res)
  };


  static get mediaType() {
    return mediaType;
  }

  event = (evt) => {
    console.log(evt);
    if (this.eventListeners.has(evt)) {
      this.eventListeners.get(evt).forEach((callback) => callback());
    }
  };

  on = (evt, callback) => {
    this.eventListeners.get(evt).push(callback);
  };

  isOpen = () => {
    return this._isOpen;
  };

  static get EVENTS() {
    return _EVENTS;
  }
}

export { RoomClient };
