export const ACTION_TYPES = {
  SET_MODAL: "SET_MODAL",
};
const initializeStream = { visible: false, header: "", render: null };

const modalReducer = (state = initializeStream, action) => {
  switch (action.type) {
    case ACTION_TYPES.SET_MODAL:
      state = action.payload;
      return state;

    default:
      return state;
  }
};

const setModal = (stream) => {
  return {
    type: ACTION_TYPES.SET_MODAL,
    payload: stream,
  };
};

export { modalReducer, setModal };
