export const ACTION_TYPES = {
    SET_REMOTE_ELEMENT: "SET_REMOTE_ELEMENT",
};
const initializeStream = [];

const remoteElementReducer = (state = initializeStream, action) => {
    switch (action.type) {
        case ACTION_TYPES.SET_REMOTE_ELEMENT:
            state = action.payload;
            return state;

        default:
            return state;
    }
};

const setRemoteElement = (element) => {
    return {
        type: ACTION_TYPES.SET_REMOTE_ELEMENT,
        payload: element,
    };
};

export { remoteElementReducer, setRemoteElement };
