import React, { PureComponent } from 'react'


class Audio extends PureComponent {
          componentDidMount = () => {
                    this.audioRef.srcObject = this.props?.srcObject;
          }

          componentDidUpdate = (prevProps) => {
                    if (prevProps.srcObject !== this.props.srcObject) {
                              this.audioRef.srcObject = this.props?.srcObject;
                    }
          }

          render() {
                    return (
                              <audio autoPlay={true} ref={(ref) => { this.audioRef = ref }} width={"100%"} height={100} style={{ background: "black", border: "2px solid #224567" }}>
                              </audio>
                    );
          }
}

export default Audio;
