import { Button } from "antd";
import Avatar from "antd/lib/avatar/avatar";
import React, { Component } from "react";
import { connect } from "react-redux";
import { setUserRoom, userRoom } from "../../global/UserRoom";
import { setLargeVideo } from "../../reducers/largeVideoReducer";
import { store } from "../../redux/store";
import "./style.scss";
const ColorList = [
  "#f56a00",
  "#7265e6",
  "#ffbf00",
  "#00a2ae,#885569",
  "#7265e6",
  "#885569",
  "#00a2ae,#885569",
  "#885569",
];
class UserComponent extends Component {
  constructor(props) {
    super(props);
    this.state = { mute: false, hasVideo: false };
  }

  componentDidMount = () => {
    const item=this.props.item;
    this.audioRef.srcObject = item?.audio?.stream;
    this.videoRef.srcObject = item?.video?.stream;
    if (this.videoRef.srcObject) {
      this.setState({ hasVideo: true });
    } else {
      this.setState({ hasVideo: false });
    }
  };

  componentDidUpdate = (prevProp) => {
    if (this.props.item !== prevProp.item) {
        const item=this.props.item;
        this.audioRef.srcObject = item?.audio?.stream;
        this.videoRef.srcObject = item?.video?.stream;
        if (this.videoRef.srcObject) {
          this.setState({ hasVideo: true });
        } else {
          this.setState({ hasVideo: false });
        }
    }
  };

  mute = (e) => {
    e.preventDefault();
    const mute = this.state.mute;
    if (!mute) {
      this.audioRef.muted = true;
    } else {
      this.audioRef.muted = false;
    }
    this.setState({ mute: !mute });
  };

  setLargeVideo = (e) => {
    this.props.onHandleSetLargeVideos();
    store.dispatch(setLargeVideo({ stream: this.videoRef.srcObject }));
  };

  render() {
    return (
      <div
        id={this.props.item.peerId}
        onClick={this.setLargeVideo}
        style={{
          border: this.props.item.active ? "2px solid red" : "none",
          position: "relative",
          width: "100%",
          height: "auto",
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
        }}
      >
        <span style={{ color: "#ddd", fontWeight: "bold", position: "absolute", maxWidth: "80%", bottom: 5 }}>
          {this.props.item?.peerName}
        </span>
        {this.state.mute ? (
          <Button
            onClick={this.mute}
            type="link"
            shape="circle"
            danger
            style={{ position: "absolute", top: 5, left: 5, zIndex: 10 }}
            icon={<i className="material-icons">volume_off</i>}
          ></Button>
        ) : (
          <Button
            onClick={this.mute}
            type="link"
            shape="circle"
            danger
            style={{ position: "absolute", top: 5, left: 5, zIndex: 10 }}
            icon={<i className="material-icons">volume_up</i>}
          ></Button>
        )}
        {!this.state.hasVideo ? (
          <div
            id="placeHodler"
            style={{
              color: "#ddd",
              display: "flex",
              alignItems: "center",
              justifyContent: "center",
              position: "absolute",
              borderRadius: "50%",
              width: 50,
              height: 50,
            }}
          >
            <Avatar
              style={{ backgroundColor: `${ColorList[Math.floor(Math.random() * 10)]}`, verticalAlign: "middle" }}
              size="large"
            >
              {this.props.item?.peerName}

              {/* {this.props.item?.peerName.match(/\b([A-Z])/g)?.join("")} */}
            </Avatar>
          </div>
        ) : null}
        <video
          ref={(ref) => {
            this.videoRef = ref;
          }}
          id="video"
          autoPlay={true}
          width={"100%"}
          height={100}
          style={{ background: "black", border: "2px solid #224567" }}
        ></video>
        <audio
          ref={(ref) => {
            this.audioRef = ref;
          }}
          id="audio"
          autoPlay={true}
        ></audio>
      </div>
    );
  }
}

function mapStateToProps(state) {
  return {
    streamRemoteState: state.streamRemoteState,
    remoteElementState:state.remoteElementState
    //         userOnRoomState: state.userOnRoomState
  };
}

function mapDispatchToProps(dispatch) {
  return {
    //         setLargeVideo: (data) => dispatch(setLargeVideo(data)),
    //         setStream: (data) => dispatch(setStream(data))
  };
}

export default connect(mapStateToProps, null)(UserComponent);
