
export const ACTION_TYPES = {
  SET_ROOMCREATE: "SET_ROOMCREATE",
};
const initializeStream =false;

const roomCreateReducer = (state = initializeStream, action) => {
  switch (action.type) {
    case ACTION_TYPES.SET_ROOMCREATE:
      state = action.payload;
      return state;
    default:
      return state;
  }
};

