import { Button } from "antd";
import Avatar from "antd/lib/avatar/avatar";
import React, { Component } from "react";
import { connect } from "react-redux";
import { userRoom } from "../../global/UserRoom";
import { setLargeVideo } from "../../reducers/largeVideoReducer";
import { setViewOneCamera } from "../../reducers/uiReducer";
import { store } from "../../redux/store";
const ColorList = [
          "#f56a00",
          "#7265e6",
          "#ffbf00",
          "#00a2ae,#885569",
          "#7265e6",
          "#885569",
          "#00a2ae,#885569",
          "#885569",
];

class MediaElement extends Component {
          constructor(props) {
                    super(props);
                    this.state = { mute: false, hasVideo: false };
                    this.audioRef = null;
                    this.videoRef = null;
          }

          componentDidMount = () => {
                    const currentUser = this.props.streamRemoteState.get(this.props.item.id);
                    this.audioRef.srcObject = currentUser?.audio?.stream;
                    this.videoRef.srcObject = currentUser?.video?.stream;
                    if (this.videoRef.srcObject) {
                              this.setState({ hasVideo: true })
                    } else {
                              this.setState({ hasVideo: false })
                    }
          }

          componentDidUpdate = (prevProp) => {
                    if (this.props.streamRemoteState !== prevProp.streamRemoteState) {
                              const currentUser = this.props.streamRemoteState.get(this.props.item.id);
                              this.audioRef.srcObject = currentUser?.audio?.stream;
                              this.videoRef.srcObject = currentUser?.video?.stream;
                              if (this.videoRef.srcObject) {
                                        this.setState({ hasVideo: true })
                              } else {
                                        this.setState({ hasVideo: false })
                              }
                    }
          }

          mute = (e) => {
                    e.preventDefault();
                    const mute = this.state.mute;
                    if (!mute) {
                              this.audioRef.muted = true;
                    } else {
                              this.audioRef.muted = false;
                    }
                    this.setState({ mute: !mute })
          }

          setLargeVideo = (e) => {
                    this.props.onHandleSetLargeVideos();
                    store.dispatch(setLargeVideo({ stream: this.videoRef.srcObject }))
                    this.props.setViewOneCamera(true)

          }


          render() {
                    const { elementWidth } = this.props;
                    return (
                              <div className="media-element" style={{ width: `${elementWidth}%`, height: `${elementWidth * 16 / 9}%` }}>
                                        <div className="media-element-container">
                                                  <div
                                                            id={this.props.item.id}
                                                            onClick={this.setLargeVideo}
                                                            style={{
                                                                      border: this.props.item.active ? "2px solid red" : "none",
                                                                      position: "relative",
                                                                      width: "100%",
                                                                      height: "100%",
                                                                      display: "flex",
                                                                      justifyContent: "center",
                                                                      alignItems: "center",
                                                            }}
                                                  >
                                                            <span style={{ color: "#ddd", fontWeight: "bold", position: "absolute", maxWidth: "80%", bottom: 5 }}>
                                                                      {this.props.item?.name}
                                                            </span>
                                                            {this.state.mute ? (
                                                                      <Button
                                                                                onClick={this.mute}
                                                                                type="link"
                                                                                shape="circle"
                                                                                danger
                                                                                style={{ position: "absolute", top: 5, left: 5, zIndex: 10 }}
                                                                                icon={<i className="material-icons">volume_off</i>}
                                                                      ></Button>
                                                            ) : (
                                                                                <Button
                                                                                          danger
                                                                                          onClick={this.mute}
                                                                                          type="link"
                                                                                          shape="circle"
                                                                                          style={{ position: "absolute", top: 5, left: 5, zIndex: 10 }}
                                                                                          icon={<i className="material-icons">volume_up</i>}
                                                                                ></Button>
                                                                      )}
                                                            {!this.state.hasVideo ? <div
                                                                      id="placeHodler"
                                                                      style={{
                                                                                color: "#ddd",
                                                                                display: "flex",
                                                                                alignItems: "center",
                                                                                justifyContent: "center",
                                                                                position: "absolute",
                                                                                borderRadius: "50%",
                                                                                width: 200,
                                                                                height: 200,
                                                                      }}
                                                            >
                                                                      <Avatar
                                                                                style={{ backgroundColor: `${ColorList[Math.floor(Math.random() * 10)]}`, verticalAlign: "middle", width: "100%", height: "100%" }}
                                                                                size="large"
                                                                      >
                                                                                {this.props.item?.name.match(/\b([A-Z])/g)?.join("")}
                                                                      </Avatar>
                                                            </div> : null}
                                                            <video
                                                                      ref={(ref) => {
                                                                                this.videoRef = ref;
                                                                      }}
                                                                      id="video"
                                                                      autoPlay={true}
                                                                      width={"100%"}
                                                                      height={"100%"}
                                                                      style={{ background: "black", border: "2px solid #224567" }}
                                                            ></video>
                                                            <audio
                                                                      ref={(ref) => {
                                                                                this.audioRef = ref;
                                                                      }}
                                                                      id="audio"
                                                                      autoPlay={true}
                                                            ></audio>
                                                  </div>
                                        </div>
                              </div>
                    );
          }
}

function mapStateToProps(state) {
          return {
                    streamRemoteState: state.streamRemoteState,
                    //         userOnRoomState: state.userOnRoomState
          };
}

function mapDispatchToProps(dispatch) {
          return {
                    setViewOneCamera: (data) => dispatch(setViewOneCamera(data)),
          };
}

export default connect(mapStateToProps, mapDispatchToProps)(MediaElement);