import React, { Component } from 'react';
import './style.scss'
class MenuComponent extends Component {
          constructor(props) {
                    super(props);
                    this.state = { fullscreen: false };
          }

          openFullscreen = () => {
                    const elem = document.documentElement;
                    if (!this.state.fullscreen) {
                              if (elem.requestFullscreen) {
                                        elem.requestFullscreen();
                              } else if (elem.webkitRequestFullscreen) { /* Safari */
                                        elem.webkitRequestFullscreen();
                              } else if (elem.msRequestFullscreen) { /* IE11 */
                                        elem.msRequestFullscreen();
                              }
                    } else {
                              document.exitFullscreen();
                    }

                    this.setState({ fullscreen: !this.state.fullscreen })
          }
          render() {
                    return (
                              <div className="menuOption">
                                        <div onClick={() => { }}> <div className="row padding"><i className="material-icons"> high_quality</i><span >Cht lng</span></div></div>
                                        <div onClick={this.openFullscreen}>
                                                  {this.state.fullscreen ? <div className="row padding"><i className="material-icons"> fullscreen_exit</i><span >Thot ton mn hnh</span></div> : <div className="row padding"><i className="material-icons"> fullscreen</i><span >Ton mn hnh</span></div>}
                                        </div>
                                        <div onClick={() => { }}> <div className="row padding"><i className="material-icons"> mic</i><span >Ghi m</span></div></div>
                                        <div onClick={() => { }}> <div className="row padding"><i className="material-icons"> videocam</i><span >Quay video</span></div></div>
                              </div>
                    );
          }
}

export default MenuComponent;