
export const ACTION_TYPES = {
          SET_VIEW_ONE_CAMERA: "SET_VIEW_ONE_CAMERA"
};
const initializeStream = {
          viewOneCamera: true,
};

const uiReducer = (state = initializeStream, action) => {
          switch (action.type) {
                    case ACTION_TYPES.SET_VIEW_ONE_CAMERA:
                              state.viewOneCamera = !state.viewOneCamera;
                              return {...state}
                    default:
                              return state;
          }
};

const setViewOneCamera = () => {
          return {
                    type : ACTION_TYPES.SET_VIEW_ONE_CAMERA,
          };
};

export { uiReducer, setViewOneCamera };
