import { combineReducers } from "redux";
import { largeVideoReducer } from "../reducers/largeVideoReducer";
import { roomCreateReducer } from "../reducers/roomCreateReducer";
import { streamReducer } from "../reducers/streamReducer";
import { userOnRoomReducer } from "../reducers/userOnRoomReducer";
import { uiReducer } from "../reducers/uiReducer";
import { streamRemoteReducer } from "../reducers/remoteReducer";
import { modalReducer } from "../reducers/modalReducer";
import { messageReducer } from "../reducers/messageReducer";
import { remoteElementReducer } from "../reducers/remoteElementReducer";
import { localElementReducer } from "../reducers/localElementReducer";

export default combineReducers({
  streamRemoteState: streamRemoteReducer,
  streamState: streamReducer,
  largeVideoState: largeVideoReducer,
  userOnRoomState: userOnRoomReducer,
  roomCreateState: roomCreateReducer,
  ui: uiReducer,
  modalState: modalReducer,
  messageState: messageReducer,
  remoteElementState: remoteElementReducer,
  localElementState: localElementReducer
});
