import React, { Component } from "react";
import PropTypes from "prop-types";
import Video from "./Video";
import Audio from "./Audio";
import { store } from "../../redux/store";
import { setLargeVideo } from "../../reducers/largeVideoReducer";
import Avatar from "antd/lib/avatar/avatar";
import { Button } from "antd";
const ColorList = [
  "#f56a00",
  "#7265e6",
  "#ffbf00",
  "#00a2ae,#885569",
  "#7265e6",
  "#885569",
  "#00a2ae,#885569",
  "#885569",
];

class UserCloneComponent extends Component {
  constructor(props) {
    super(props);
    this.state = { muted: false };
  }

  onVideoClick = (video) => {
    this.props.onHandleSetLargeVideos();
    store.dispatch(setLargeVideo({ stream: video.srcObject }));
  };

  render() {
    const { muted } = this.state;
    return (
      <div
        className="videos-user-component"
      >
        <Button
          style={{ position: "absolute", top: 0, left: 0,zIndex:2 }}
          onClick={() => {
            this.setState({ muted: !muted });
          }}
          icon={muted ? <i className="material-icons">volume_off</i> : <i className="material-icons">volume_up</i>}
        ></Button>
        <div
          className="peerName"
        >
          {this.props.item?.peerName}
        </div>
        {this.props.item.video.length === 0 ? (
          <div
            id="placeHodler"
            style={{
              color: "#ddd",
              display: "flex",
              alignItems: "center",
              justifyContent: "center",
              position: "absolute",
              borderRadius: "50%",
              width: 50,
              height: 50,
            }}
          >
            <Avatar
              style={{ backgroundColor: `${ColorList[Math.floor(Math.random() * 10)]}`, verticalAlign: "middle" }}
              size="large"
            >
              {this.props.item?.peerName}

              {/* {this.props.item?.peerName.match(/\b([A-Z])/g)?.join("")} */}
            </Avatar>
          </div>
        ) : null}
        {this.props.item.video.map((video, index) => {
          return <Video key={index} srcObject={video.stream} onClick={this.onVideoClick} />;
        })}
        <Audio srcObject={this.props.item?.audio?.stream} muted={muted} />
      </div>
    );
  }
}

UserCloneComponent.propTypes = {};

export default UserCloneComponent;
