export const ACTION_TYPES = {
          SET_REMOTE_STREAM: "SET_REMOTE_STREAM",
};
const initializeStream = new Map();

const streamRemoteReducer = (state = initializeStream, action) => {
          switch (action.type) {
                    case ACTION_TYPES.SET_REMOTE_STREAM:
                              state = action.payload;
                              return state;

                    default:
                              return state;
          }
};

const setRemoteStream = (stream) => {
          return {
                    type: ACTION_TYPES.SET_REMOTE_STREAM,
                    payload: stream,
          };
};

export { streamRemoteReducer, setRemoteStream };
