import React, { Component } from 'react'
import PropTypes from 'prop-types'
import './style.scss'

export default class MicOption extends Component {
          // static propTypes = {
          //           prop: PropTypes
          // }

          constructor(props) {
                    super(props);
                    this.state = { mic: [], speaker: [] };
          }


          componentDidMount = async () => {
                    const devices = await window.navigator.mediaDevices.enumerateDevices();
                    const mic = [];
                    const speaker = []
                    for (let index = 0; index < devices.length; index++) {
                              const deviceInfo = devices[index];
                              if (deviceInfo.kind === 'audioinput') {
                                        mic.push({ deviceInfo, active: deviceInfo.deviceId === "default" ? true : false })
                              }
                              if (deviceInfo.kind === 'audiooutput') {
                                        speaker.push({ deviceInfo, active: deviceInfo.deviceId === "default" ? true : false })
                              }

                    }
                    this.setState({ mic, speaker })
          }

          onActiveOptionMic = (item, index) => {
                    const currentMic = this.state.mic;
                    for (let i = 0; i < currentMic.length; i++) {
                              const element = currentMic[i];
                              if (i === index) {
                                        element.active = true;

                              } else {
                                        element.active = false;
                              }
                    }
                    this.setState({ mic: currentMic })
          }

          onActiveOptionCamera = (item, index) => {
                    const currentSpeaker = this.state.speaker;
                    for (let i = 0; i < currentSpeaker.length; i++) {
                              const element = currentSpeaker[i];
                              if (i === index) {
                                        element.active = true;
                              } else {
                                        element.active = false;
                              }
                    }
                    this.setState({ speaker: currentSpeaker })
          }

          render() {
                    return (
                              <div className="micOption" key="mic">
                                        <div className="row"><i className="material-icons"> mic</i><h2 >Microphones</h2></div>
                                        {this.state.mic.map((item, index) => {
                                                  return <div onClick={() => this.onActiveOptionMic(item, index)} className={item.active ? "active" : ""} key={"mic" + index} ><div className="row padding">{item.active ? <i className="material-icons"> check</i> : <i className="material-icons"> mic</i>}<span >{item.deviceInfo.label}</span></div></div>
                                        })}
                                        <div className="row"><i className="material-icons"> volume_up</i><h2 >Speaker</h2></div>
                                        {this.state.speaker.map((item, index) => {
                                                  return <div onClick={() => this.onActiveOptionCamera(item, index)} className={item.active ? "active" : ""} key={"speaker" + index}><div className="row padding">{item.active ? <i className="material-icons"> check</i> : <i className="material-icons"> volume_up</i>}<span >{item.deviceInfo.label}</span></div></div>
                                        })}
                              </div>
                    )
          }
}