import React, { Component } from 'react';
import ReactStopwatch from 'react-stopwatch';

class Clock extends Component {
          constructor(props) {
                    super(props);
                    this.state = {};
                    this.count = 0;
          }

          render() {
                    return (
                              <ReactStopwatch
                                        seconds={0}
                                        minutes={0}
                                        hours={0}
                                        render={({ formatted, hours, minutes, seconds }) => {
                                                  return (
                                                            <div style={{ position: "absolute", zIndex: 2, background: "#fff", fontWeight: 600, top: 5, right: 5 }}>
                                                                      <p style={{ margin: 4, display: "flex", alignItems: "center", fontSize: 12 }}>
                                                                                <i style={{ fontSize: 17}} className="material-icons">alarm</i>
                                                                                {formatted}
                                                                      </p>
                                                            </div>
                                                  );
                                        }}
                              />
                    );
          }
}

export default Clock;