import React, { Component } from "react";
import { connect } from "react-redux";
import { setLargeVideo } from "../../reducers/largeVideoReducer";
import { setStream } from "../../reducers/streamReducer";
import Video from "../Video/Video";
import Audio from "../Audio/Audio";
import { Button } from "antd";
import UserComponent from "../Video/UserComponent";
import Item from "antd/lib/list/Item";
import { userRoom } from "../../global/UserRoom";
import { store } from "../../redux/store";
import { setUserOnRoom } from "../../reducers/userOnRoomReducer";
import UserCloneComponent from "../Video/UserCloneComponent";
class RemoteVideo extends Component {
  constructor(props) {
    super(props);
    this.audioRef = null;
  }

  onHandleSetLargeVideo = (item, index) => {
    this.setActive(index);
  };

  setActive = (index) => {
    const currentStream = { ...this.props.streamState };
    const local = currentStream.local;
    for (let j = 0; j < local.length; j++) {
      const element = local[j];
      element.active = false;
    }
    for (let i = 0; i < this.props.userOnRoomState.length; i++) {
      const element = this.props.userOnRoomState[i];
      if (i === index) {
        element.active = true;
      } else {
        element.active = false;
      }
    }
    this.props.setUserOnRoom(this.props.userOnRoomState);
    this.props.setStream({ ...currentStream });
  };

  render() {
    return (
      <div className="remote-video-component" >
        {this.props.remoteElementState.map((item, index) => {
          return (
            <UserCloneComponent
              key={index}
              item={item}
              onHandleSetLargeVideos={() => this.onHandleSetLargeVideo(item, index)}
            />
          );
        })}
        
      </div>
    );
  }
}
function mapStateToProps(state) {
  return {
    streamState: state.streamState,
    userOnRoomState: state.userOnRoomState,
    remoteElementState: state.remoteElementState,
  };
}

function mapDispatchToProps(dispatch) {
  return {
    setLargeVideo: (data) => dispatch(setLargeVideo(data)),
    setStream: (data) => dispatch(setStream(data)),
    setUserOnRoom: (data) => dispatch(setUserOnRoom(data)),
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(RemoteVideo);
